
.submit-button {
  display: block;
  color: #fff;
  cursor: pointer;
  background-color: #0084fa;
  border: none;
  border-radius: 10px;
  font-size: 10pt;
  padding: 10px;
  min-width: 100px;
  text-align: center;
}
.slider {
  padding: 20px 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background: #eee;
}
.slider_list {
  list-style: none;
  white-space: nowrap;
  padding: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  overflow-x: auto;
  max-width: 500px;
  margin: 0 auto;
}
.slider_list-item {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  white-space: normal;
  text-align: center;
  overflow: hidden;
  min-width: 60px;
  max-width: 100px;
}
.slider_list-item:not(:first-child) {
  margin-left: 20px;
}
.slider_list-item:first-child {
  padding-left: 20px;
}
.slider_list-item:last-child {
  padding-right: 20px;
}
.slider_text {
  margin-bottom: 0;
  margin-top: 0px;
  font-size: 12px;
}
.slider_img-holder {
  border: 2px solid #0084fa;
  -webkit-box-shadow: 2px 1px 5px 0px #7dc2ff;
          box-shadow: 2px 1px 5px 0px #7dc2ff;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: #fff;
}
.slider_img {
  -o-object-fit: cover;
     object-fit: cover;
  -o-object-position: center;
     object-position: center;
  border-radius: 50%;
  width: 50px;
  height: 50px;
}


